import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import * as serviceWorker from './serviceWorker';

function Square(props) {
    return (
        <button className="square" id = {props.number} onClick={props.onClick}>
            {props.value}
        </button>
    );
}

class Board extends React.Component {
    renderSquare(i) {
        return (
            <Square
                value={this.props.squares[i]}
                onClick={() => this.props.onClick(i)}
                number = {"sq"+i}
            />
        );
    }

    render() {
        return (
            <div>
                <div className="board-row">
                    {this.renderSquare(0)}
                    {this.renderSquare(1)}
                    {this.renderSquare(2)}
                </div>
                <div className="board-row">
                    {this.renderSquare(3)}
                    {this.renderSquare(4)}
                    {this.renderSquare(5)}
                </div>
                <div className="board-row">
                    {this.renderSquare(6)}
                    {this.renderSquare(7)}
                    {this.renderSquare(8)}
                </div>
            </div>
        );
    }
}

class Game extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            history: [
                {
                    squares: Array(9).fill(null),
                    info: Array(9).fill(null),
                    usedPos: null
                }
            ],
            stepNumber: 0,
            xIsNext: true,
            width: 0,
            height: 0,
            onMobile: false
        };
        this.updateWindowDimensions = this.updateWindowDimensions.bind(this);

    }

    handleClick(i) {
        const history = this.state.history.slice(0, this.state.stepNumber + 1);
        const current = history[history.length - 1];
        const squares = current.squares.slice();
        if (calculateWinner(squares) || squares[i]) {
            return;
        }
        squares[i] = this.state.xIsNext ? "X" : "O";
        this.setState({
            history: history.concat([
                {
                    squares: squares,
                    info: "Player: " + squares[i] + " | Pos: (" + (Math.floor(i%3)+1) + ", " +(Math.floor(i/3)+1) + ")",
                    usedPos: i,
                }
            ]),
            stepNumber: history.length,
            xIsNext: !this.state.xIsNext
        });
        var win = calculateWinner(squares);
        if(win){
            for(let i = 0; i < 3; i++)
                document.getElementById("sq" + win[i]).style.background = "#ccc";
        }
        for (let i = 0; i < 9; i++) {
            document.getElementById("sq" + i).style.fontWeight = null;
        }
        document.getElementById("sq" + i).style.fontWeight = "bold";
    }

    jumpTo(step) {
        if(this.state.stepNumber !== step) {
            const history = this.state.history[step];
            for (let i = 0; i < 9; i++) {
                document.getElementById("sq" + i).style.background = null;
                document.getElementById("sq" + i).style.fontWeight = null;
            }
            this.setState({
                stepNumber: step,
                xIsNext: (step % 2) === 0
            });
            if(step !== 0)
                document.getElementById("sq" + history.usedPos).style.fontWeight = "bold";
            var win = calculateWinner(history.squares)
            if (win){
                for(let i = 0; i < 3; i++)
                    document.getElementById("sq" + win[i]).style.background = "#ccc";
            }
        }
    }

    render() {
        const history = this.state.history;
        const current = history[this.state.stepNumber];
        const winner = calculateWinner(current.squares);
        const moves = history.map((step, move) => {
            const desc = move ?
                'Go to move #' + move :
                'Go to game start';
            return (
                <li key={move}>
                    <button onClick={() => this.jumpTo(move)}>{desc}</button> {step.info}
                </li>
            );
        });

        let status;
        if (winner) {
            status = "Winner: " + current.squares[winner[0]];
        } else if (this.state.stepNumber === 9){
            status = "We Have A Tie!"
        }
        else {
            status = "Next player: " + (this.state.xIsNext ? "X" : "O");
        }

        return (
            <div className="game">
                <div className="status">{status}</div>
                <div className="game-board">
                    <Board
                        squares={current.squares}
                        onClick={i => this.handleClick(i)}
                    />
                </div>
                <div className="game-info">
                    <ol>{moves}</ol>
                </div>
            </div>
        );
    }

    componentDidMount() {
        this.updateWindowDimensions();
        window.addEventListener('resize', this.updateWindowDimensions);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateWindowDimensions);
    }

    updateWindowDimensions() {
        let mobile = window.innerWidth < 500;
        this.setState({ width: window.innerWidth, height: window.innerHeight, onMobile: mobile });
    }
}

// ========================================

ReactDOM.render(<Game />, document.getElementById("root"));

